import React from 'react'
import MainContainer from './MainContainer'

const Main = React.createClass({
  render () {
    return (
      <MainContainer>
        <span>I'm a span from Main.js</span>
        <div>
          <span>I'll render on every page because I'm in the parent component</span>
        </div>

        {this.props.children}

      </MainContainer>
    );
  },
});

export default Main



