import React from 'react'
import { Toggle } from 'belle'

const styles = {
  centeredContainer: {
    verticalAlign: 'center'
  },
  subheader: {
    fontSize: '50px',
    color: '#333',
    fontWeight: 150,
  },
  messageBox:{
    backgroundColor:'#ef553a',
    width:300,
    paddingTop:10,
    paddingBottom:20,
    paddingLeft:20,
    paddingRight:20,
    borderRadius:10
  },
  messageBoxTitleText:{
      fontWeight:'bold',
      color:'#000',
      textAlign:'center',
      fontSize:20,
      marginBottom:10
  },
  messageBoxBodyText:{
      color:'#fff',
      fontSize:16
  },
  content: {
    backgroundColor:'#ebeef0',
    flex:1,
    alignItems:'center',
    flexDirection: 'row',
  },
}


export default function Form (props) {
  return (
    <div style={styles.content}>
      <div style={styles.messageBox}>
        <div>
          <p style={styles.messageBoxTitleText}>A simple mesage</p>
        </div>
        <div>
            <p style={styles.messageBoxBodyText}>This is just a dummy sample it will help us to see the alignment in action.</p>
        </div>
      </div>
    </div>
  )
}



