import React from 'react'

const styles = {
  centeredContainer: {
    verticalAlign: 'center'
  },
  subheader: {
    fontSize: '50px',
    color: '#333',
    fontWeight: 150,
  },
  Aligner: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'column',
    backgroundColor: 'red',
  },
  AlignerItem: {
    alignSelf: 'center',
    backgroundColor: 'blue'
  }

}

function MainContainer(props) {
  return (
    <div className='col-sm-12 text-center'>
      <div >
        {props.children}
      </div>
    </div>
  );
}

export default MainContainer



